// components/Sidebar.tsx
import { Home, Users, CreditCard, Settings } from 'lucide-react';

const menuItems = [
  { name: 'Dashboard', icon: <Home />, path: '/' },
  { name: 'Clientes', icon: <Users />, path: '/clientes' },
  { name: 'Pagamentos', icon: <CreditCard />, path: '/pagamentos' },
  { name: 'Configurações', icon: <Settings />, path: '/config' },
];

export default function Sidebar() {
  return (
    <aside className="h-screen w-64 bg-white dark:bg-zinc-900 border-r border-zinc-200 dark:border-zinc-800 px-4 py-6 flex flex-col">
      <h2 className="text-xl font-bold mb-6 text-zinc-800 dark:text-zinc-100">Stripe Clone</h2>
      <nav className="space-y-2">
        {menuItems.map((item, idx) => (
          <a
            key={idx}
            href={item.path}
            className="flex items-center gap-3 px-3 py-2 rounded-lg text-zinc-700 dark:text-zinc-200 hover:bg-zinc-100 dark:hover:bg-zinc-800 transition"
          >
            {item.icon}
            <span>{item.name}</span>
          </a>
        ))}
      </nav>
    </aside>
  );
}
